import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { EducationBgComponent } from './page/education-bg/education-bg.component';
import { SpecializedSkillComponent } from './page/specialized-skill/specialized-skill.component';
import { ProjectExperienceComponent } from './page/project-experience/project-experience.component';
import { SelfIntroductionComponent } from './page/self-introduction/self-introduction.component';

// 滑动显示的组件
import { SwiperModule } from 'ngx-useful-swiper';
// 卡片展示组件
import { CardModule } from 'primeng/card';
// PrimeNg动画库
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
// step组件库
import {StepsModule} from 'primeng/steps';
// 按钮组件库
import {ButtonModule} from 'primeng/button';

@NgModule({
  declarations: [
    AppComponent,
    EducationBgComponent,
    SpecializedSkillComponent,
    ProjectExperienceComponent,
    SelfIntroductionComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    SwiperModule,
    CardModule,
    StepsModule,
    ButtonModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



