import { Component, OnInit, ViewChild } from '@angular/core';
import { MenuItem } from 'primeng/api';
import { Steps } from 'primeng/steps';

@Component({
  selector: 'app-education-bg',
  templateUrl: './education-bg.component.html',
  styleUrls: ['./education-bg.component.css']
})
export class EducationBgComponent implements OnInit {
  @ViewChild('stepcoms') pSteps: Steps;

  public items: MenuItem[];
  public activeIndex = 2;

  public MsgShows = [
    '2015年获得第三届天地图应用开发大赛 三等奖',
    '2016年毕业设计题目是空间语义相似度研究',
    '2017年获得年度公司级事业部优秀员工'
  ];

  constructor() { }

  ngOnInit() {
    this.items = [
      {
        label: '高中 就读于中山纪念中学',
      },
      {
        label: '大学 就读于中南大学 地理信息专业', command: (event: any) => {
        }
      },
      {
        label: '毕业 就职于广东省南方数码科技股份公司',
        command: (event: any) => {
        }
      }
    ];
  }

}



